import { Component, OnInit } from '@angular/core';
import { InicioService } from '../../../providers/Almacenamiento/inicio.service';
import { UsuariosLoginService } from '../../../providers/Api/UsuariosLogin.service';
import { ToastServices } from '../../../providers/Utilidades/ToastService.service';

@Component({
  selector: 'app-clave',
  templateUrl: './clave.page.html',
  styleUrls: ['./clave.page.scss'],
})
export class ClavePage implements OnInit {

  validado = false;

  clave = '';
  confirmar = '';
  valido = '';

  vl = false;

  paises= [];
  ciudades = [];
  ciudad = false;
  load = false;

  pais = '';
  miciudad = '';

  correo = '';
  tel = 0;
  ncompleto = '';
  placa = '';

  cargatodo = false;
  role = '';

  constructor(
    private almacenamiento: InicioService,
    private apiUsuario: UsuariosLoginService,
    private toast: ToastServices
  ) { }

  ngOnInit() {
  }

  ionViewWillEnter() {
    this.datos();

  }

  refrescando( event ) {
    this.cargatodo = false;
    this.datos().then(() => {
      event.detail.complete();
    }).catch(() => {
      event.detail.complete();
    })
  }


  async validar() {
    this.vl = true;
    const info = await this.almacenamiento.obtenerToken();

    if (info) {
      this.apiUsuario.validarClave(info.token, {clave: this.valido}).subscribe((data: any) => {
        if (data.exe) {
          this.validado = true;
        } else {
          this.toast.toastError('la contraseña no es correcta');
        }
        this.vl = false;
      }, (err) => {
        this.vl = false;
        this.toast.toastError('No se pudo validar la contraseña, inténtelo más tarde');
      })
    }
  }


  async actualizar() {
    if (this.clave === this.confirmar  && this.clave !== '' && this.confirmar !== '') {
      const info = await this.almacenamiento.obtenerToken();

      if (info) {
        this.apiUsuario.editarClaves(info.token, {clave: this.confirmar}).subscribe((data: any) => {
          if (data.exe) {
            this.validado = false;
            this.toast.toastExitoso('Contraseña actualizada');
          } else {
            this.toast.toastError('No se pudo actualizar la contraseña, inténtelo más tarde');
          }
        }, (err) => {
          this.toast.toastError('No se pudo actualizar la contraseña, inténtelo más tarde');
        })
      }
    } else {
      this.toast.toastError('Las contraseñas no coínciden');
    }
  }

  async datos() {
    const info = await this.almacenamiento.obtenerToken();

    if (info) {
      this.role = info.role;
      this.apiUsuario.usuarioDatos(info.token).subscribe((data: any) => {
        if (data.exe) {
          this.ncompleto = data.response.nombrecompleto;
          this.tel = data.response.tel;
          this.correo = data.response.correo;
          this.placa = data.response.placa ? data.response.placa   : '';
          this.pais = data.response.pais;
          this.miciudad = data.response.ciudad;
          this.cargatodo = true;
          this.listarPaises();
        } else {
          this.toast.toastError('No se pudo traer los datos del usuario, inténtelo más tarde');
        }
      }, (err) => {
        this.toast.toastError('No se pudo traer los datos del usuario, inténtelo más tarde');
      })
    }
  }


  
  listarPaises() {
    this.apiUsuario.listarPaises().subscribe((data: any) => {
      if (data.exe) {
        this.paises = data.response;

        if (this.pais !== '') {
          this.listarCiudades({detail: {value: this.pais}});
        }
      } else {
        this.paises.push('Colombia');
      }
    }, (err) => {
      this.paises.push('Colombia');
    })
  }


  listarCiudades( event ) {
    this.pais = event.detail.value;
    this.miciudad = this.miciudad !== '' ? this.miciudad : '';
    const _id = this.paises.filter((item) => item.nombre === this.pais)[0]._id;
    this.apiUsuario.listarCiudades(_id).subscribe((data: any) => {
     if (data.exe) {
       this.ciudades = data.response;
       this.ciudad= true;
     } else {
       this.toast.toastError('No se pudo traer las ciudades, intente registrarse más tarde');
     }
   }, (err) => {
     this.toast.toastError('No se pudo traer las ciudades, intente registrarse más tarde');
   })
 }

 seleccionarCiudad( event ) {
   this.miciudad = event.detail.value;
 }


 
 async guardar() {
  const info = await this.almacenamiento.obtenerToken();

  if (info) {
    this.role = info.role;
    // tslint:disable-next-line:max-line-length
    this.apiUsuario.editarUusuario(info.token, {ciudad: this.miciudad, pais: this.pais, placa: this.placa, nombrecompleto: this.ncompleto, telefono: this.tel, correo: this.correo }).subscribe((data: any) => {
      if (data.exe) {
        this.toast.Especial('Uusario editado correctamente');
      } else {
        this.toast.toastError('No se pudo editar los datos del usuario, inténtelo más tarde');
      }
    }, (err) => {
      this.toast.toastError('No se pudo editar los datos del usuario, inténtelo más tarde');
    })
  }
}
}
